import request from '@/utils/request'

// 获取当前登录的个人资料
export const getUserInfo = () => {
  return request({
    method: 'get',
    url: '/app/v1_0/user/profile'
  })
}

// 修改当前登录的个人资料
export const editUserInfo = (data) => {
  return request({
    method: 'patch',
    url: '/app/v1_0/user/profile',
    data
  })
}

// 修改当前登录的头像
export const editPhoto = (data) => {
  return request({
    method: 'patch',
    url: '/app/v1_0/user/photo',
    data
  })
}
