<template>
  <div>
    <van-picker show-toolbar
                title="性别"
                :columns="['男','女']"
                @cancel='$emit("close")'
                @confirm="onConfirm"
                @change="onChange"
                :default-index="sexIndex" />
  </div>
</template>

<script>
export default {
  props: {
    sexIndex: [String, Number]
  },
  data () {
    return {
      gengerIndex: this.sexIndex
    }
  },
  methods: {
    onConfirm () {
      this.$toast.loading({
        message: '保存中',
        forbidClick: true,
        duration: 0
      })
      this.$emit('updateSex', this.gengerIndex)
    },
    onChange (picker, values, index) {
      this.gengerIndex = index
    }
  }
}
</script>

