<template>
  <div>
    <van-nav-bar title="设置昵称"
                 left-text="取消"
                 right-text="完成"
                 @click-left="onClickLeft"
                 @click-right="onClickRight" />
    <van-field v-model.trim="message"
               rows="2"
               autosize
               type="textarea"
               maxlength="7"
               placeholder="请输入昵称"
               show-word-limit></van-field>
  </div>
</template>

<script>
export default {
  props: {
    username: String
  },
  data () {
    return {
      message: this.username
    }
  },
  methods: {
    onClickLeft () {
      this.$emit('close')
    },
    onClickRight () {
      this.$toast.loading({
        message: '保存中',
        forbidClick: true,
        duration: 0
      })
      this.$emit('updateName', this.message)
    }
  }
}
</script>

