<template>
  <div>
    <van-datetime-picker v-model="currentDate"
                         type="date"
                         title="选择年月日"
                         @cancel='$emit("close")'
                         @confirm="onConfirm"
                         :min-date="minDate"
                         :max-date="maxDate" />
  </div>
</template>

<script>
export default {
  props: {
    birthday: String
  },
  data () {
    return {
      minDate: new Date(1960, 10, 31),
      maxDate: new Date(),
      currentDate: new Date(this.birthday || null)
    }
  },
  methods: {
    onConfirm () {
      this.$toast.loading({
        message: '保存中',
        forbidClick: true,
        duration: 0
      })
      this.$emit('updateBirthday', this.currentDate)
    }
  }
}
</script>

