<template>
  <div class="photoChange">
    <img :src="img"
         ref="img"
         alt="">
    <div class="btn">
      <div @click="$emit('close')">取消</div>
      <div @click="onConfirm">完成</div>
    </div>
  </div>
</template>

<script>
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import { editPhoto } from '@/api/editUserInfo'
export default {
  props: {
    img: String
  },
  data () {
    return {
      cropper: null
    }
  },
  mounted () {
    const image = this.$refs.img
    this.cropper = new Cropper(image, {
      viewMode: 1, // 让裁剪框不能超出图片的大小
      dragMode: 'none', // 只让拖动裁剪框，后面的图片不能拖动
      aspectRatio: 1, // 裁剪框的比例
      // autoCropArea: 1, // 裁剪框的大小会默认撑满
      background: false // 关闭自带的背景
    })
  },
  methods: {
    // 裁剪完成，保存触发
    onConfirm () {
      this.$toast.loading({
        message: '保存中',
        forbidClick: true,
        duration: 0
      })
      // console.log(this.cropper.getData())
      this.cropper.getCroppedCanvas().toBlob(async (blob) => {
        // console.log(blob)
        // 接口要求的参数是Content-Type为multipart/form-data格式的对象
        // 所以不能直接用 photo:blob
        const formData = new FormData()
        formData.append('photo', blob)
        try {
          const { data: res } = await editPhoto(formData)
          // console.log(res)
          // 关闭弹层
          this.$emit('close')
          // 更新视图
          this.$emit('updataPhoto', res.data.photo)
          this.$toast.success('更新头像成功')
        } catch (err) {
          this.$toast('更新失败，请稍后重试')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.photoChange {
  background-color: #000;
  height: 100%;
  img {
    max-width: 100%;
  }
  .btn {
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    div {
      width: 90px;
      height: 90px;
      font-size: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
    }
  }
}
</style>
