<template>
  <div class="edit-container">
    <van-nav-bar title="个人信息"
                 right-text="保存"
                 left-arrow
                 @click-left="onClickLeft"
                 @click-right="onClickRight" />
    <div class="userinfoBox">
      <input type="file"
             hidden
             ref="file"
             @change="onFileChange">
      <van-cell is-link
                @click="$refs.file.click()">
        <div slot="title">头像</div>
        <van-image slot="default"
                   class="avatar"
                   round
                   :src="userProfile.photo"></van-image>
      </van-cell>
      <van-cell is-link
                @click="editNameShow=true">
        <div slot="title">昵称</div>
        <div slot="default">{{userProfile.name}}</div>
      </van-cell>
      <van-cell is-link
                @click="editSexShow=true">
        <div slot="title">性别</div>
        <div slot="default">{{userProfile.gender===0?'男':'女'}}</div>
      </van-cell>
      <van-cell is-link
                @click="editBornShow=true">
        <div slot="title">生日</div>
        <div slot="default">{{userProfile.birthday}}</div>
      </van-cell>
    </div>
    <!-- 修改昵称的弹出层 -->
    <van-popup v-model="editNameShow"
               position="bottom"
               :style="{ height: '100%' }">
      <edit-name @close='editNameShow=false'
                 v-if="editNameShow"
                 @updateName='changeName'
                 :username='userProfile.name'></edit-name>
    </van-popup>
    <!-- 修改性别的弹出层 -->
    <van-popup v-model="editSexShow"
               position="bottom">
      <edit-sex :sexIndex='userProfile.gender'
                v-if="editSexShow"
                @updateSex='changeSex'
                @close='editSexShow=false'></edit-sex>
    </van-popup>
    <!-- 修改生日的弹出层 -->
    <van-popup v-model="editBornShow"
               position="bottom">
      <edit-born :birthday='userProfile.birthday'
                 v-if="editBornShow"
                 @updateBirthday='changeBorn'
                 @close='editBornShow=false'></edit-born>
    </van-popup>
    <!-- 修改头像的弹出层 -->
    <!-- <van-popup v-model="isShow"
               class="editImgBox"
               position="bottom">
      <div class="editImg-btn">

        <van-cell>
          <div slot="title">拍照</div>
          <van-icon name="photograph"
                    slot="right-icon" />
        </van-cell>
        <van-cell @click="$refs.file.click()">
          <div slot="title">照片图库</div>
          <van-icon name="photo"
                    slot="right-icon" />
        </van-cell>
        <van-cell>
          <div slot="title">浏览</div>
          <van-icon name="weapp-nav"
                    slot="right-icon" />
        </van-cell>
      </div>
      <div class="editImg-cancel">
        <van-button>取消</van-button>
      </div>
    </van-popup> -->
    <!-- 预览图片的弹出层 -->
    <van-popup v-model="photoShow"
               position="bottom"
               :style="{ height: '100%' }">
      <edit-img :img='img'
                v-if="photoShow"
                @close='photoShow=false'
                @updataPhoto='userProfile.photo=$event'></edit-img>
    </van-popup>
  </div>
</template>

<script>
import dayjs from 'dayjs'
import { getUserInfo, editUserInfo } from '@/api/editUserInfo'
import editName from './components/edit-name'
import editSex from './components/edit-sex'
import editBorn from './components/edit-born'
import editImg from './components/edit-img'
export default {
  data () {
    return {
      userProfile: {},
      editNameShow: false,
      editSexShow: false,
      editBornShow: false,
      isShow: false,
      photoShow: false,
      img: null
    }
  },
  components: {
    editName,
    editSex,
    editBorn,
    editImg
  },
  methods: {
    onClickLeft () {
      this.$router.push('/my')
    },
    // 保存
    onClickRight () {
      console.log(this.userInfo)
    },
    // 获取用户个人资料
    async loadUserInfo () {
      const { data: res } = await getUserInfo()
      // console.log(res)
      this.userProfile = res.data
    },
    // 更新昵称
    async changeName (data) {
      this.editNameShow = false
      await editUserInfo({ name: data })
      this.userProfile.name = data
      this.$toast.success('修改昵称成功')
    },
    // 更新性别
    async changeSex (data) {
      this.editSexShow = false
      await editUserInfo({ gender: data })
      this.userProfile.gender = data
      this.$toast.success('修改性别成功')
    },
    // 更新生日
    async changeBorn (data) {
      this.editBornShow = false
      const date = dayjs(data).format('YYYY-MM-DD')
      // console.log(date)
      await editUserInfo({ birthday: date })
      this.userProfile.birthday = date
      this.$toast.success('修改生日成功')
    },
    // 选择上传图片时触发
    onFileChange () {
      this.isShow = false
      // 获取文件对象
      const file = this.$refs.file.files[0]
      // 基于文件对象获取blob数据
      const data = window.URL.createObjectURL(file)
      // console.log(data)
      this.photoShow = true
      this.img = data
      // 如果关闭后再选择同一张图片不会触发change事件，
      // 所以要每次关闭时清空
      this.$refs.file.value = ''
    }
  },
  created () {
    this.loadUserInfo()
  }
}
</script>

<style lang="scss" scoped>
.edit-container {
  ::v-deep.van-nav-bar {
    background-color: #3296fa;
    .van-icon-arrow-left {
      font-size: 38px;
      color: #fff;
    }
    .van-nav-bar__title {
      font-size: 38px;
      color: #fff;
    }
    .van-nav-bar__text {
      font-size: 30px;
      color: #fff;
    }
  }
  .userinfoBox {
    margin: 10px 0;
    .avatar {
      width: 50px;
      height: 50px;
    }
  }
  .van-cell {
    height: 93px;
    font-size: 28px;
    color: #333;
    border-bottom: 1px solid #e8e8e8;
    .textColor {
      color: #3296fa;
    }
  }
  .editImgBox {
    background-color: transparent;
    width: 95%;
    margin-left: 2.5%;
    .editImg-btn {
      background-color: #1f1c21;
      margin-bottom: 20px;
      border-radius: 20px;
      overflow: hidden;
      .van-cell {
        background-color: #1f1c21;
        border-bottom: 0;
        .van-cell__title {
          color: #fff;
        }
        .van-icon {
          color: #4495ff;
          font-size: 38px;
          top: 8px;
        }
      }
    }
    .editImg-cancel {
      margin-bottom: 20px;
      .van-button {
        width: 100%;
        background-color: #1f1c21;
        border: 0;
        border-radius: 20px;
        color: #3a90ff;
      }
    }
  }
}
</style>
